/* Fonts */
@import url("https://fonts.googleapis.com/css2?family=Kumbh+Sans:wght@300&display=swap");
.account {
  font-family: sans-serif;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  gap: 20px;
  height: 91vh;
  font-family: "Kumbh Sans", sans-serif;
}

.account input[type="text"],
.account input[type="password"] {
  height: 32px;
  width: 80%;
  border-radius: 5px;
  padding: 10px;
  border: 1px solid rgba(0, 0, 0, 0.2);
}

.account input[type="submit"] {
  margin-top: 25px;
  margin-bottom: 20px;
  cursor: pointer;
  font-size: 15px;
  background: #c19a6b;
  border: 1px solid #c19a6b;
  color: #fff;
  padding: 10px 20px;
  border-radius: 10px;
}

.account input[type="submit"]:hover {
  background: #e7c59b;
}

.button-container {
  display: flex;
  justify-content: center;
}

.login-form {
  background-color: white;
  height: 50%;
  width: 25%;
  padding: 50px;
  border: 1px solid rgba(0, 0, 0, 0.3);
  border-radius: 20px;
}

.list-container {
  display: flex;
}

.error {
  color: red;
  font-size: 12px;
}

.title {
  display: flex;
  font-size: 38px;
  text-transform: uppercase;
  margin-bottom: 40px;
  justify-content: center;
  align-items: center;
}

.input-container {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  gap: 8px;
  margin: 10px;
}
.logged-in {
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 20px;
}
