.homepage {
  position: relative;
  max-width: 100%;
  overflow: hidden;
  margin-top: -160px;
}

.homepage .video-container {
  position: relative;
  top: 0;
  width: 100%;
  padding-bottom: 56.25%; /* 16:9 Aspect Ratio */
  border-top: 1px solid #333333;
}
.video-container video {
  object-fit: cover;
}
.text {
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  padding: 20px;
  color: #fff;
}
.text h1,
.text-2 h1 {
  font-size: 3.1rem;
  margin: 0 0 0 12px;
  color: rgba(255, 255, 255);
  text-shadow: 1px 3px 4px #333333;
}
