.button {
  width: 20vw;
  height: 4vh;
  background-color: rgba(0, 0, 0, 0.2);
  border: 2px solid rgba(255, 255, 255, 0.6);
  font-size: 1.06rem;
  border-radius: 20px;
  color: white;
  cursor: pointer;
  backdrop-filter: blur(15px);
  text-shadow: 0px 1px 5px #333333;
  transition: all 300ms ease;
}
.text-2 {
  position: absolute;
  bottom: 0;
  left: auto;
  right: 0;
  width: 50%;
  padding: 20px;
  box-sizing: border-box;
  color: #fff;
  text-align: right; /* dodanie wyjustowania do prawej strony */
}
.button:hover {
  border: 2px solid rgba(255, 255, 255, 1);
}

.homepage .video-container video {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}
